/* MiniGame.jsx */

// Components
import Board from "../Components/Board"
import Keyboard from "../Components/Keyboard"
import HeaderNavi from '../Components/HeaderNavi'
import { boardDefault, generateWordSet } from '../Components/Words';
import GameOver from "../Components/GameOver";
import {FlexRowDiv} from '../Components/GlobalStyled/CommonStyled'

// css
import styled from 'styled-components'

//Hooks
import { createContext, useEffect, useState } from 'react';
export const AppContext = createContext();

const MiniGame = () =>{
    //   boardDefault  5X6
    const [board, setBoard] = useState(boardDefault);
    //   state
    const [currAttempt, setCurrAttempt] = useState({attempt:0, letterPos: 0})
    // word bank    state
    const [wordSet, setWordSet] = useState(new Set());
    //    state
    const [correctWord, setCorrectWord] = useState("");
    //      state
    const [disabledLetters, setDisabledLetters] = useState([]);
    // /   state
    const [gameOver, setGameOver] = useState({
      gameOver: false,
      guessedWord: false,
    });

    //   useEffect()
    useEffect(()=>{
        //    
        //  
        generateWordSet().then((word)=>{
            //console.log(word.wordSet)
            // ,  
            setWordSet(word.wordSet);
            setCorrectWord(word.todaysWord);
        })
    },[])
    
    //   
    const onSelectLetter = (keyVal) => {
        if(currAttempt.letterPos > 4) return;
        const newBoard = [...board];
        newBoard[currAttempt.attempt][currAttempt.letterPos] = keyVal;
        setBoard(newBoard)
        setCurrAttempt({...currAttempt, letterPos: currAttempt.letterPos + 1})
    }

    //  
    const onDelete = () => {
        if(currAttempt.letterPos === 0) return;
        const newBoard = [...board];
        newBoard[currAttempt.attempt][currAttempt.letterPos - 1] = "";
        setBoard(newBoard)
        setCurrAttempt({...currAttempt, letterPos: currAttempt.letterPos - 1})
     }

     //  
    const onEnter = () => { 
        //  5   
        if (currAttempt.letterPos !== 5) return;

        //    ,   
        let currWord ="";
        for(let i=0; i<5; ++i){
            currWord += board[currAttempt.attempt][i];
            //console.log(currWord);
        }

        //      
        if(wordSet.has(currWord)){
            //   + 1
            setCurrAttempt({attempt: currAttempt.attempt + 1, letterPos : 0})
            //console.log(currWord);
        }else{
            alert("Word Not Found")
        }

        //    , 
        if (currWord === correctWord) {
            setGameOver({ gameOver: true, guessedWord: true });
            return;
          }
        console.log(currAttempt);
        //     
        if (currAttempt.attempt === 5) {
        setGameOver({ gameOver: true, guessedWord: false });
        return;
        }
        
    }
    return (
        <div className="App">

          {/*   */}
          <AppContext.Provider
            value={{
              board,
              setBoard,
              currAttempt,
              setCurrAttempt,
              correctWord,
              onSelectLetter,
              onDelete,
              onEnter,
              setDisabledLetters,
              disabledLetters,
              gameOver,
            }}
          >
            <nav>
             {/*  */}
             <HeaderNavi />
          </nav>
            <Wrap>
              <div className="game">
                {<Board />}
                {/*   ,    */}
                {gameOver.gameOver ? <GameOver /> : <Keyboard />}
              </div>
              <TextWrap>
                <p>MINI GAME RULES</p>
                <p>1. guess the word in six goes or less</p>
                <p>2. A correct letter turns <GreenText>green</GreenText></p>
                <p>3. A correct letter in the wrong place turns <YellowText>yellow</YellowText></p>
                <p>4. Letters can be used more than once</p>
                <p>5. Answers are never plurals</p>
              </TextWrap>
            </Wrap>
          </AppContext.Provider>
        </div>
      );
    }

export default MiniGame;

const Wrap = styled(FlexRowDiv)`
`;

const TextWrap = styled.div`
  margin-right: 200px;
  font-size: 35px;
  color: white;
  text-shadow: 6px 6px 10px #3CCEBE;
`;

const GreenText = styled.span`
    color: #528d4e;
    filter: brightness(200%);
    font-size: 50px;
`;

const YellowText = styled.span`
    color: #b49f39;
    filter: brightness(200%);
    font-size: 50px;
`;